				let viewHeight = this.$refs.viewBox.offsetHeight;
				if(listHeight > viewHeight){
					//滚动
					this.doScroll = true;
					let el1 = this.$refs.memberList;
					let el2 = this.$refs.memberList2;
					el1.style.top = 0;
					el2.style.top = listHeight;
					let y1 = 0;
					let y2 = listHeight;
					this.intervalId = setInterval(()=>{
						y1--;
						y2--;
						if(y1 <= -listHeight){
							y1 = listHeight;
						}
						if(y2 <= -listHeight){
							y2 = listHeight;
						}
						el1.style.top = y1 + 'px';
						el2.style.top = y2 + 'px';
						
					},45)
					
				}else{
					this.doScroll = false;
				}
				clearTimeout(timeOutId);
